.list {
    box-shadow: inset 0px -15px 20px -15px black;
    height: 180px;
    border-radius: 5px;
    width: 120px;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    transition: 0.3s;
    cursor: pointer;
    background-position: center;
}

.list:hover {
    background-size: 120% 120%;
}

