@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Epilogue&display=swap');


.main {
  height: fit-content;
  display: grid;
  padding: 5px;
  grid-template-areas:
    'header detail'
    'midle midle'
    'footer footer';
  color: inherit;
}

.main2 {
  display: grid;
  padding: 5px;
  grid-template-areas:
    'header detail'
    'midle midle'
    'footer footer';
  color: aliceblue;
}

.sampul {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px;
  grid-area: header;
}

.sampul img {
  height: 300px;
  border-radius: 5px;

}

.detail {
  color: inherit;
  margin: 5px;
  grid-area: detail;
  height: fit-content;
}

.detail h3, h5 {
  color: inherit;
  margin: inherit;
  font-family: 'Poppins', sans-serif;
}

h3 {
  font-weight: 700;
}

h5 {
  border-bottom: 1px solid rgb(194, 203, 210);
  height: 20px;
  font-weight: 300;
}

.detail a {
  display: inline-block;
  align-items: center;
  font-size: 10px;
  font-family: 'poppins', sans-serif;
  color: aliceblue;
  background-color: gray;
  text-decoration: none;
  height: 20px;
  text-align: center;
  padding: 3px;
  padding-top: 1.5px;
  border: 1px solid gray;
  border-radius: 5px;
  margin: 3px;
}

.status {
  display: inline-block;
  font-size: 10px;
  color: inherit;
  text-decoration: none;
  height: 15px;
  text-align: center;
  margin: 3px;
  font-family: 'Poppins', sans-serif;
  font-weight: 700;
}
.detail section {
  margin-top: 10px;
}
.deskripsi{
  color: inherit;
}

.deskripsi p {
  font-size: 0.8em;
  text-overflow: ellipsis;
  display: inline-block;
  line-height: 1.3em;
  overflow: scroll;
  width: 95%;
  height: 130px;
  transform: translate(-5px);
  color: inherit;
  font-family: 'Poppins', sans-serif;
}

.deskripsi p::-webkit-scrollbar {
  width: 2px;
  height: 0px;
  opacity: 50%;
}

.deskripsi p::-webkit-scrollbar-track {
  box-shadow: inset aliceblue;
}

.deskripsi p::-webkit-scrollbar-thumb {
  background: grey;
  border-radius: 10px;
}

.deskripsi {
  margin: 10px;
  height: 70px;
  width: 100%;
}
.header {
  transition: color 3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  margin: 5px;
  font-family: 'Poppins', sans-serif;
  font-size: medium;
  font-weight: 500;
  color: inherit;
  height: 25px;
  border-bottom: 1px solid rgb(194, 203, 210);
}

.chapter {
  grid-area: midle;
  color: gray;
}

.chapter2 {
  grid-area: midle;
  color: aliceblue;
}
.borderbtm {
  transition: color 3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  margin: 3px;
  font-family: 'Poppins', sans-serif;
  font-size: medium;
  font-weight: 500;
  color: inherit;
  height: 3px;
  border-bottom: 1px solid rgb(194, 203, 210);
}
.link{
  margin: 10px;
  text-decoration: none;
  color: inherit;
  font-family: 'poppins', sans-serif;
}

.chapter{
  color: inherit;
}
.active{
  transition:3s step-start;
  border-bottom: 3px solid gray;
}
.conNav{
  display: flex;
  padding: 3px;
}

@media only screen and (max-width: 700px) {
  .main {
    grid-template-areas:
      'header'
      'midle'
      'footer';
  }

  .detail {
    grid-area: midle;
  }

  .chapter {
    grid-area: footer;
  }

  .header {
    margin-top: 50px;
  }

  .search {
    width: 50%;
  }
}