@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Epilogue&display=swap');

*,
html {
    margin: 0;
    padding: 0;
    font-family: 'Epilogue', sans-serif;
    scroll-behavior: smooth;
    box-sizing: border-box;
}

body {
    overflow-x: hidden;
    transition: background-color 3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    background-color: aliceblue;
    color: rgb(47, 42, 42);
    user-select: none;
}

.body2 {
    overflow-x: hidden;
    background-color: rgb(47, 42, 42);
    user-select: none;
    color: aliceblue;
}

.load {
    overflow: hidden;
}

.loading {
    z-index: 10;
    position: fixed;
    /* animation: myAnim 2s ease 0s infinite normal forwards; */
    height: 100px;
    width: 100px;
    background-repeat: no-repeat;
    background-image: url(assets/Icon/rizlogo.svg);
    background-size: cover;
}

.loading--bg {
    z-index: 9;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    position: fixed;
    width: 100%;
    backdrop-filter: blur(30px);
}

@keyframes myAnim {

    0%,
    50%,
    100% {
        opacity: 1;
    }

    25%,
    75% {
        opacity: 0;
    }
}

.container {
    position: fixed;
    backdrop-filter: blur(20px);
    align-items: center;
    border-color: gray;
    height: 40px;
    width: 100%;
    top: 0;
    /* box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .2); */
    z-index: 4;
    border-bottom: solid 1px gray;
}

.bg--nav {
    height: 40px;
    background-color: gray;
    width: 100%;
}

li {
    list-style: none;
}

.style {
    align-items: center;
    position: relative;
    display: flex;
    gap: 20px;
    padding-right: 5px;
}

.bg {

    background-color: gray;
    height: 40px;
    width: 100%;
    opacity: 80%;
    position: fixed
}

.logo {
    transition: 3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    margin-top: 4.5px;
    opacity: 90%;
    margin-left: 15px;
    /* border: 1px solid gray; */
    border-radius: 5px;
    height: 30px;
    width: 30px;
    background-repeat: no-repeat;
    background-image: url(assets/Icon/rizlogo.svg);
    background-size: cover;
}

.kotak::-webkit-scrollbar {
    width: 0px;
    height: 2px;
}

.kotak::-webkit-scrollbar-track {
    box-shadow: inset aliceblue;
}

.kotak::-webkit-scrollbar-thumb {
    background: grey;
    border-radius: 10px;
}

::-webkit-scrollbar {
    width: 2px;
    height: 2px;
}

::-webkit-scrollbar-track {
    box-shadow: inset aliceblue;
}

::-webkit-scrollbar-thumb {
    border: 2.5px solid rgb(181, 185, 189);
    background: grey;
    border-radius: 10px;
}

body::-webkit-scrollbar {
    width: 10px;
    height: 0px;
    background-color: rgb(181, 185, 189);
}

body::-webkit-scrollbar-track {
    box-shadow: inset aliceblue;
}

body::-webkit-scrollbar-thumb {
    background: grey;
    border-radius: 0;
}

.kotak {
    padding: 10px;
    display: flex;
    gap: 5px;
    height: 150px;
    overflow: scroll;
    scroll-snap-type: x mandatory;
    scroll-padding: 250px;
    column-gap: 20px;
}

.kartu {
    scroll-snap-align: start;
    border-radius: 5px;
    margin-left: 50px;
    background-repeat: no-repeat;
    background-size: contain;
}

.header {
    transition: color 3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    margin: 5px;
    margin-bottom: 10px;
    font-family: 'Poppins', sans-serif;
    font-size: medium;
    font-weight: 500;
    opacity: 80%;
    color: inherit;
    height: 25px;
}

.containerlist {
    height: 300px;
    overflow: hidden;
}

#kotak2 {
    padding: 10px;
    display: flex;
    gap: 100px;
    flex-wrap: wrap;
    flex-direction: row;
    overflow: scroll;
}

.pilihlist {
    color: gray;
    padding: 5px;
}



@-webkit-keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.serial {
    margin: 5px;
    display: block;
    overflow-y: scroll;
    border-radius: 10px;
    border: solid 2px gray;
    height: 230px;
    animation: fadeIn 1s;
    background-color: rgba(180, 181, 181, 0.3);
}

.serial h4 {
    z-index: 2;
    position: absolute;
    width: fit-content;
    border-radius: 5px 0px 5px 0px;
    background-color: gray;
    font-weight: lighter;
    padding: 5px;
    color: aliceblue;
}


.web {
    animation: fadeIn 1s;
    margin: 5px;
    overflow: scroll;
    border-radius: 10px;
    border: solid 2px gray;
    height: 230px;
    background-color:rgba(180, 181, 181, 0.3);
}

.web h4 {
    z-index: 2;
    position: absolute;
    width: fit-content;
    border-radius: 5px 0px 5px 0px;
    background-color: gray;
    font-weight: lighter;
    padding: 5px;
    color: aliceblue;
}

.cardo {
    justify-content: center;
    align-content: center;
    padding: 27px;
    display: flex;
    flex-wrap: wrap;
    gap: 25px;
}

.label {
    display: table-cell;
    text-align: center;
    text-justify: center;
    font-size: small;
    position: relative;
    height: 175px;
    width: 115px;
    vertical-align: bottom;
    text-shadow: 0 1px 3px black;
    color: white;
}

.decorationlabel {
    display: block;
    height: 180px;
    width: 100%;
    text-decoration: none;
}

.fppost {
    position: static;
    margin: 3px;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

.ffp {
    display: block;
    flex: 30%;
    overflow: scroll;
    height: 640px;
    padding-bottom: 10px;
    width: fit-content;
    margin: auto;
    border: 2px solid gray;
    border-radius: 5px;
    background-color:rgba(180, 181, 181, 0.3);
}

.fchat {
    display: block;
    flex: 30%;
    padding-left: 5px;
    padding-right: 5px;
    overflow-y: scroll;
    overflow-x: hidden;
    height: 640px;
    padding-bottom: 10px;
    width: fit-content;
    margin: auto;
    border: 2px solid gray;
    border-radius: 5px;
    background-color:rgba(180, 181, 181, 0.3);
}

.elf {
    height: 20px;
}

.headerfp {
    z-index: 1;
    position: absolute;
    width: 100px;
    border-radius: 3px 0px 3px 0px;
    background-color: gray;
    font-weight: lighter;
    padding: 5px;
    color: aliceblue;
}

.ig {
    transform: translateX(-5%);
}

.pilihlist button {
    font-family: 'Poppins', sans-serif;
    text-align: center;
    background-color: gray;
    color: aliceblue;
    border: solid 1px gray;
    height: 25px;
    width: 45px;
    cursor: pointer;
    border-radius: 3px;
    transition: cubic-bezier(0.075, 0.82, 0.165, 1);
    margin-right: 5px;
}

.pilihlist button:hover {
    background-color: #5c5c5c;
}

.info {
    width: 100%;
    background-color: gray;
    height: 100%;
}

.deskripsifoot {
    width: 100%;
    color: aliceblue;
    margin-top: 10px;
    position: relative;
    line-height: 1.6;
    font-size: smaller;
    /* margin-left: 65px; */
    width: 100%;
    padding: 10px;
    text-align: center;
    text-justify: center;
}

.sosmed {
    margin: 5px;
    height: 30px;
    border-bottom: 1px solid aliceblue;
}

.headerinfo {
    transition: 3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    margin: 5px;
    font-family: 'Poppins', sans-serif;
    font-size: medium;
    font-weight: 500;
    color: inherit;
    height: 25px;
}

.headerinfo2 {
    transition: 3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    margin: 5px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: medium;
    font-weight: light;
    color: aliceblue;
    height: 25px;
}

.toggle {
    background-color: aliceblue;
    border-radius: 3px;
    display: inline-block;
    border: none;
    cursor: pointer;
    color: rgb(47, 42, 42);
    font-family: 'Poppins', sans-serif;
    font-size: 10px;
    font-weight: bold;
    padding: 4.5px 20px;
    text-decoration: none;
    position: fixed;
    z-index: 5;
}

.toggle:hover {
    transition: background-color 0.3s ease-in;
    background-color: #b3b3b3;
}

.toggle2 {
    background-color: rgb(47, 42, 42);
    border-radius: 3px;
    border: none;
    display: inline-block;
    cursor: pointer;
    color: aliceblue;
    font-family: 'Poppins', sans-serif;
    font-size: 10px;
    font-weight: bold;
    padding: 4.5px 20px;
    text-decoration: none;
    position: fixed;
    z-index: 5;
}

.toggle2:hover {
    transition: background-color 0.3s ease-in;
    background-color: #b3b3b3;
}



.sosmed {
    align-content: center;
    justify-content: center;
    display: flex;
    gap: 15px;
}

.sosmed a {
    margin-top: 5px;
    text-decoration: none;
    font-family: 'Poppins', sans-serif;
    color: aliceblue;
    font-size: small;
}

.sosmed a:hover {
    color: #5c5c5c;
    font-size: small;
    transition: 1s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.power p {
    font-size: small;
    font-family: 'Poppins', sans-serif;
}

.bawah {
    align-content: center;
    justify-content: center;
    display: flex;
    gap: 10px;
    border-top: 1px solid aliceblue;
    margin: 5px;
}

.bawah>p {
    font-family: 'Poppins', sans-serif;
    color: aliceblue;
    position: relative;
    line-height: 1.6;
    font-size: 0.9em;
    font-size: smaller;
}

.search {
    background: #b4b4b4;
    border: none;
    height: 25px;
    width: 150px;
    border-radius: 2px;
    box-sizing: border-box;
    display: inline-block;
    padding: 10px;
    font-family: 'Poppins', sans-serif;
    color: aliceblue;
}

.div-cari {
    padding-right: 25px;
    display: flex;
    justify-content: start;
    align-items: center;
    height: 40px;
}

.search::placeholder {
    color: gray;
}

.search:focus::placeholder {
    color: aliceblue;
    transition: color 0.6s linear;
}

.search:focus {
    transition: border-bottom 0.6s linear;
    outline: none;
    border-bottom: solid 1px rgb(167, 170, 173);
}

input::-webkit-calendar-picker-indicator {
    opacity: 0%;
}
.div-mode {
    width: 90%;
    display: flex;
    justify-content: end;
    align-items: center;
    height: 40px;
}

/* Responsive */
@media only screen and (max-width: 600px) {
    .ffp, .fchat {
        flex: 90%;
        overflow: visible;
        height: fit-content;
    }

    .fchat {
        display: block;
    }

    .text {
        font-size: smaller;
    }

}